import { Component, OnInit, EventEmitter, Input, Output, forwardRef, ViewEncapsulation } from '@angular/core';
import { FormGroup, ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { BaseComponent } from 'src/app/modules/common/components/base/base.component';

@Component({
  selector: 'app-input-checkbox',
  templateUrl: './input-checkbox.component.html',
  styleUrls: ['./input-checkbox.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => InputCheckboxComponent),
      multi: true
    }
  ],
  encapsulation: ViewEncapsulation.None
})
export class InputCheckboxComponent implements OnInit, ControlValueAccessor {
  @Output() check = new EventEmitter();
  // tslint:disable-next-line: no-input-rename tslint:disble-next-line: variable-name
  @Input('value') _value: boolean;
  @Input() label = '';
  onChange: any = () => {};
  onTouched: any = () => {};

  ngOnInit() {
  }

  get value() {
    return this._value;
  }

  set value(value) {
    this._value = value;
    this.onChange(value);
    this.onTouched();
  }

  writeValue(value) {
    this._value = value;
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  onCheck(value: boolean) {
    this.check.emit(value);
    this._value = value;
  }

}
