import { Component, OnInit, forwardRef, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'mbaas-predictive-list',
  templateUrl: './predictive-select-list.component.html',
  styleUrls: ['./predictive-select-list.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PredictiveSelectListComponent),
      multi: true
    }
  ]
})
export class PredictiveSelectListComponent implements ControlValueAccessor {

  @ViewChild('search') search;

  // tslint:disable-next-line: no-inferrable-types
  disabled: boolean = false;
  etiqueta: string;
  rex: string;
  lista: boolean;

  interval: any;
  // tslint:disable-next-line: no-input-rename variable-name
  @Input('value') _value: string;
  @Input() error: boolean;
  // tslint:disable-next-line: no-inferrable-types
  @Input() placeHolder: string = 'Seleciona una opción';
  // tslint:disable-next-line: no-inferrable-types
  @Input() restric: string = '';
  // tslint:disable-next-line: no-inferrable-types
  @Input() maxLength: number = Infinity;
  @Input() datos: Array<any> = [];
  @Input() done: (item: any) => any = (item) => item.label;
  // tslint:disable-next-line: member-ordering no-inferrable-types
  @Input() limit: number = 10;
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() change = new EventEmitter();
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() focus = new EventEmitter();
  // tslint:disable-next-line: no-output-native member-ordering
  @Output() focusout = new EventEmitter();
  @Input() getLabel: (item) => string = ( item ) => item.label;
  @Input() getValue: (item) => string = ( item ) => item.value;
  onChange: any = () => { };
  onTouched: any = () => { };

  constructor() {
    this.rex = '';
    this.etiqueta = '';
    this.lista = false;
    this.change.emit('');
  }

  touch(): void {
    this.lista = true;
    this.onTouched();
  }

  ocutar(): void {
    this.lista = false;
  }

  setValue(item): void {
    this.etiqueta = this.getLabel(item);
    this.value = this.getValue(item);
    this.search.nativeElement.value = this.etiqueta;
  }

  get value() {
    return this._value;
  }

  set value(value) {
    this._value = value;
    this.onChange(value);
    this.onTouched();
    this.change.emit(value);
  }

  writeValue(value) {
    const resto = this.datos.filter(item => this.getValue(item) === value);
    if ( resto.length !== 0 ) {
      this.etiqueta = this.getLabel(resto[0]);
    } else {
      this.etiqueta = '';
    }
    if ( this.search ) {
      this.search.nativeElement.value = this.etiqueta;
    } else {
      this.interval = setInterval(() => this.set(), 100);
    }
    this._value = value;
    this.change.emit(resto.length !== 0 ? resto[0] : '');
  }


  set(): void {
    if ( !this.search ) {
      return;
    }
    const resto = this.datos.filter(item => this.getValue(item) === this.value);
    if ( resto.length !== 0 ) {
      this.etiqueta = this.getLabel(resto[0]);
    } else {
      this.etiqueta = '';
    }
    this.search.nativeElement.value = this.etiqueta;
    clearInterval(this.interval);
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  sendFocus(): void {
    this.focus.emit();
    this.rex = '';
  }

  sendFocusOut(): void {
    this.focusout.emit();
  }

  buscar(
    items: any[] = [],
    regx: any = '',
    done: (item: any) => any = this.done,
    limit: number = this.limit
  ): any {
    if (!items) {
        return [];
    }
    let num = 0;
    const temp = items.filter(item => {
      if ( num < limit && done(item).toLowerCase().indexOf(regx.toLowerCase()) !== -1 ) {
        num++;
        return true;
      }
      return false;
    });
    return temp;
  }

  input($event: any): void {
    this.value = '';
    this.lista = true;
    this.rex = this.filtro($event.target.value);
    $event.target.value = this.rex;
  }

  filtro(valor: string): string {
    const regex = this.restric === '' ? '.*' : `[${this.restric}]*`;
    valor = valor.match(new RegExp(regex, 'g')).reduce( (a, b) => a + b, '');
    if (
      valor.length > this.maxLength
    ) {
      return valor.substring(0, this.maxLength);
    }
    return valor;
  }
}
