import { Component, OnInit, EventEmitter, Input, Output, forwardRef } from '@angular/core';
import { FormGroup, ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'app-input-switch',
  templateUrl: './input-switch.component.html',
  styleUrls: ['./input-switch.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => InputSwitchComponent),
      multi: true
    }
  ]
})
export class InputSwitchComponent implements OnInit, ControlValueAccessor  {
  @Output() switch = new EventEmitter();
  // tslint:disable-next-line: no-input-rename tslint:disble-next-line: variable-name
  @Input('value') _value: boolean;
  @Input() label = '';
  @Input() addClassSwith = 'contCheckboxSiNo contCheckboxSiNo--small';
  onChange: any = () => { };
  onTouched: any = () => { };

  // tslint:disable-next-line: no-inferrable-types member-ordering
  @Input() addClass: string = 'formGroup__label formGroup__label--large';
  // tslint:disable-next-line: no-inferrable-types member-ordering
  @Input() bold: boolean = false;

  constructor() { }

  ngOnInit() {
  }

  get value() {
    return this._value;
  }

  set value(value) {
    this._value = value;
    this.onChange(value);
    this.onTouched();
  }

  writeValue(value) {
    this._value = value;
  }

  registerOnTouched(fn) {
    this.onTouched = fn;
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  onSwitch(value: boolean) {
    this.switch.emit(value);
    this._value = value;
  }

}
